import React from 'react'
import { useLocation } from 'react-router-dom'
import { Row, Col, Form, Spin } from 'antd'
import styled from 'styled-components'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import useRouteLeaveConfirm from '@/hooks/useRouteLeaveConfirm'
import useAuth from '@/hooks/useAuth'
import {
  ReportTemplateSelect,
  useAllReportTemplates,
} from '@/services/report-template'
import { DoctorSelect, useDoctors } from '@/services/pathology'
import Input from '@/components/input'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import { rules } from '@/utils/form'
import history from '@/utils/history'
import { toQueryString } from '@/utils/request'
import { queryString } from '@/utils/webHelper'
import { REPORT_INPUT } from '@/constants/routes'

const FormItem = styled(Form.Item)``

export default function ReportInquiry() {
  useTitle('輸入報告')

  useBreadcrumb([{ name: '輸入報告' }])

  React.useEffect(() => {
    const handleKeyPress = (evt: KeyboardEvent) => {
      if (evt.key === `Enter`) {
        handleOk()
      }
    }

    window.addEventListener('keydown', handleKeyPress)

    return () => {
      window.removeEventListener('keydown', handleKeyPress)
    }
  }, []) // eslint-disable-line

  const { orreqno } = queryString(useLocation().search)

  React.useEffect(() => {
    if (orreqno) {
      form.setFieldsValue({ orreqno })
    }
  }, [orreqno]) // eslint-disable-line

  const { user } = useAuth()

  const handleConfirm = useRouteLeaveConfirm('/')

  const [form] = Form.useForm()

  const { loading, doctorById } = useDoctors()
  const { loading: loadingTemplates } = useAllReportTemplates()

  const handleOk = () => {
    form.validateFields().then(values => {
      history.push(`${REPORT_INPUT}?${toQueryString(values)}`)
    })
  }

  useHeaderContent(
    <div>
      <Button
        color="oultine"
        className="text-white border-white bg-transparent"
        onClick={handleOk}>
        確認
      </Button>
      <Button
        color="outline"
        className="ml-3 text-white border-white bg-transparent"
        onClick={handleConfirm}>
        取消
      </Button>
    </div>
  )

  return (
    <>
      <Spin spinning={loading || loadingTemplates}>
        <Page style={{ minHeight: 'calc(100vh - 203px)' }}>
          <Row justify="center">
            <Col span={20}>
              <div className="bg-white p-12 py-24 mb-8 rounded-lg shadow-lg">
                <Form
                  form={form}
                  layout="inline"
                  className="flex justify-center"
                  hideRequiredMark={true}
                  colon={false}>
                  <FormItem
                    className="text-lg"
                    label="審核醫師"
                    rules={[rules.required]}
                    {...(doctorById[user.id]
                      ? { initialValue: user.name }
                      : {})}
                    name="doctor">
                    <DoctorSelect
                      allowClear
                      bordered={false}
                      size="large"
                      placeholder="選擇醫師"
                      style={{ width: 160 }}
                    />
                  </FormItem>
                  <FormItem
                    label="申請序號"
                    rules={[rules.required]}
                    name="orreqno">
                    <Input
                      size="large"
                      bordered={false}
                      placeholder="輸入申請序號"
                      style={{ width: 230 }}
                    />
                  </FormItem>
                  <FormItem
                    rules={[rules.required]}
                    name="templateName"
                    label="報告格式">
                    <ReportTemplateSelect
                      placeholder="選擇報告模版"
                      size="large"
                      style={{ width: 230 }}
                    />
                  </FormItem>
                </Form>
                <div className="mt-6"></div>
              </div>
            </Col>
          </Row>
        </Page>
      </Spin>
    </>
  )
}
